body {
  font-family: Montserrat, sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul, ol, li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button, a {
  cursor: pointer;
}

input, textarea {
  box-sizing: border-box;
  outline: none;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 480px) {
  .container {
    width: 320px;
    padding-left: 22px;
    padding-right: 22px;
  }
}

@media screen and (min-width: 768px) {
  .container {
