  padding: 28px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-window__close-btn {
  width: 21px;
  height: 21px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 4px;
  right: 4px;
}

.modal-window__close-icon {
  fill: var(--txt-color-blck);
